const WelcomePhase = ({phase, setPhase}) =>{
    const click = () =>{
        setPhase(phase + 1)
    };

    return(<div className="phase-block">
        <div className="annotationReg">
            <h1>
              !
            </h1>
            <p>
            </p>
        </div>
        <button className="continue-button" onClick={click}>
        
        </button>
    </div>);
}

export default WelcomePhase;