import InputWithError from "../../../animations/input-error-field.js";

const TopicEnter = ({topic, setTopic, error}) => {
    const activateHint = (value) => {
        setTopic(value);
    };

    return(<div className="blockPhase">
        <h1>
              ?
        </h1>
        <div className="create-topic-input-size">
            <InputWithError placeholder={""} value={topic} setValue={setTopic} error={error}></InputWithError>
        </div>
        <div className="button-hints">
            <button className="hint" onClick={() => activateHint("")}>
            
            </button>
            <button className="hint" onClick={() => activateHint("")}>
            
            </button>
            <button className="hint" onClick={() => activateHint("")}>
            
            </button>
            <button className="hint" onClick={() => activateHint("")}>
            
            </button>
        </div>
    </div>)
}

export default TopicEnter;